import React, { useEffect, useState } from 'react';
import { Container, AppBar, Typography, Grow, Grid, TextField, InputAdornment, Switch, Button, FormControlLabel, CssBaseline, Box, Slide, Card } from '@material-ui/core';
import { SearchRounded } from '@material-ui/icons'
import { useDispatch } from 'react-redux';
// import 'flag-icon-css/css/flag-icon.css'

import { getEvents, getOldEvents } from './actions/events';
import Events from './components/Events/Events';
import useStyles from './styles';
import EnrollDialog from './components/EnrollDialog/EnrollDialog';

import { useLocale } from './Localization';
import Subscribe from './components/SubscribeDialog/Subscribe';
import OldEvents from './components/Events/OldEvents';

const App = () => {
    const [currentEnter, setCurrentEnter] = useState(null);
    const [searchString, setSearch] = useState(null);
    const [subOpen, setSubOpen] = useState(false);
    const [oldVisible, setoldVisible] = useState(false);
    const [donateShow, setDonateShow] = useState(false);
    const classes = useStyles();
    const dispatch = useDispatch();
    const locale = useLocale('main');

    useEffect(() => {
        dispatch(getEvents());
    }, [dispatch]);

    const handleSubclose = () => {
        setSubOpen(false);
    };

    const handleOld = () => {
        setoldVisible(!oldVisible);
        dispatch(getOldEvents());
    } 

    const switchDonateShow = () => {
        if (donateShow) setDonateShow(false);
        else setDonateShow(true);
    }

    return (
        <div>
        <CssBaseline />
        <AppBar className={classes.appBar} position="static" color="primary">
            {/* <Link to="/dashboard" className={classes.login}>Login</Link> */}
            <Typography variant="h2" align="center">{locale.pageTitle}</Typography>
            {/* <div className={classes.languages}>
                <IconButton onClick={() => changeLocale("en")} variant="outlined" ><div className="flag-icon flag-icon-gb"></div></IconButton>
                <IconButton onClick={() => changeLocale("fi")} variant="outlined" ><div className="flag-icon flag-icon-fi"></div></IconButton>
            </div> */}
        </AppBar>
        <Container>
            <AppBar className={classes.textHolder} position="static" color="inherit">
                <Box color="primary" bgcolor="text.secondary" className={classes.heading}><Typography variant="h4">Info</Typography></Box>
                <Typography className={classes.mainText} variant="body1" align="center">{locale.mainParagraph}</Typography>
                <Button variant="contained" size="medium" color="primary" style={{margin: '2rem 0 1rem 0'}} onClick={() => setSubOpen(true)} disabled>{locale.globalSubscribe}</Button>
                <Subscribe open={subOpen} handleClose={handleSubclose} />
            </AppBar>
            <AppBar className={classes.textHolder} position="static" color="inherit">
                <Box color="primary" bgcolor="text.secondary" className={classes.heading}><Typography variant="h4">{locale.newEventsTitle}</Typography></Box>
                <Container className={classes.filters}>
                    <TextField className={classes.search} name="tags" variant="outlined" placeholder={locale.searchBox} shrink="true" InputProps={{
                        startAdornment: (
                            <InputAdornment position="start">
                                <SearchRounded />
                            </InputAdornment>
                        ),
                    }} 
                    inputProps={{style: { padding: 7 }}}
                    onChange={(event) => setSearch(event.target.value)}
                    />
                    {/* <FormControlLabel
                        control={<Switch />} 
                        label={locale.searchIncludeOld}
                        disabled
                    /> */}
                </Container>
                <Grow in>
                    <Container style={{padding: '0'}}>
                        <Grid className={classes.container} container justify="space-between" alignItems="stretch">
                            <Events searchString={searchString} setCurrentEnter={setCurrentEnter} />
                        </Grid>
                    </Container>
                </Grow>
            </AppBar>
            <EnrollDialog currentEnter={currentEnter} setCurrentEnter={setCurrentEnter} />
            <Container className={classes.loadOldButton}>
                <Button variant="contained" color="primary" onClick={handleOld} >{locale.loadOld}</Button>
            </Container>
            <AppBar className={classes.textHolder} position="static" color="inherit" style={{display: oldVisible ? "flex": "none"}}>
                <Typography className={classes.heading} variant="h4" align="center">{locale.oldEventsTitle}</Typography>
                <Grow in>
                    <Container style={{padding: '0'}}>
                        <Grid className={classes.container} container justify="space-between" alignItems="stretch">
                            <OldEvents />
                        </Grid>
                    </Container>
                </Grow>
            </AppBar>
            {/* <div className={classes.supportWrapper}>
                <Button className={classes.supportButton} onClick={() => {setSupport(s => !s)}} variant="outlined" >
                Technical support
                </Button>
                <Paper elevation={4} className={classes.supportPaper}>
                    I case of technical difficulties please contact <a href="mailto:delion@saeril.net">delion@saeril.net</a>
                </Paper>
            </div> */}
        </Container>
        <div className={classes.donateBox}>
            <Button className={classes.donateShowButton} variant="outlined" onClick={switchDonateShow}>
                {donateShow ? "Hide me" : "Donations welcome!" }
            </Button>
            <Slide direction="right" in={donateShow} >
            <Card>
                This page was built using free student labour. If you liked it consider donating, I would really appreciate it. Donations either through Vilkkoni or directly to here.
            <form style={{marginTop: '0.5rem'}} action="https://www.paypal.com/donate" method="post" target="_blank">
            <input type="hidden" name="hosted_button_id" value="P9JZHXTGTUJ56" />
            <input type="image" src="https://www.paypalobjects.com/en_US/i/btn/btn_donateCC_LG.gif" border="0" name="submit" title="" alt="Donate with PayPal button" />
            <img alt="" border="0" src="https://www.paypal.com/en_FI/i/scr/pixel.gif" width="1" height="1" />
            </form>
            </Card>
            </Slide>
        </div>
        </div>
    );
}

export default App; 