import * as dayjs from 'dayjs'
import localeformat from 'dayjs/plugin/localizedFormat'

import 'dayjs/locale/en-gb'
import 'dayjs/locale/fi'
import languageEN from './Localized/english.json';
import languageFI from './Localized/finnish.json';

let locale = languageFI;

dayjs.extend(localeformat);

export const useLocale = (section) => {

    if (section)
        return locale[section] ? locale[section] : locale;
    else 
        return locale;
    // const loc = useLocation()

    // const lang = new URLSearchParams(loc.search).get("lang");
    // if (lang === "fi") {
    //     locale = languageFI;
    //     dayjs.locale('fi');
    // }
    // else {
    //     locale = languageEN;
    //     dayjs.locale('en-gb');
    // }
    // return lang;
}

export const useChangeLocale = () => {
    return (lang) => {
        if (lang === "fi") {
            locale = languageFI;
            dayjs.locale('fi');
        }
        else {
            locale = languageEN;
            dayjs.locale('en-gb');
        }
        // history.replace({
        //     search: `lang=${lang}`
        // })
    };
}