import React, { useState, useEffect } from 'react';
import { Grid, CircularProgress } from '@material-ui/core'
import { useSelector } from 'react-redux';

import useStyles from './styles.js';

import { locale } from '../../Localization'
import OldEvent from './Event/OldEvent.js';

const OldEvents = () => {
    const classes = useStyles();
    const events = useSelector((state) => state.oldevents);

    return (
        !events.length ? (<CircularProgress style={{margin: "0 auto"}}/>) : (
            <div style={{width: '100%', display: 'grid'}}>
                <Grid className={classes.mainContainer} container alignItems="stretch" direction="column" spacing={3} width="auto">
                    {events.map((event) => (
                        <OldEvent key={event._id} event={event} />
                    ))}
                </Grid>
            </div>
        )
    );
}

export default OldEvents;