import axios from 'axios'

const url = 'https://vilkkoni-server.herokuapp.com/events';
const validate_url = 'https://vilkkoni-server.herokuapp.com/validate';
const sub_url = 'https://vilkkoni-server.herokuapp.com/sub';

export const fetchEvents = () => axios.get(url);
export const fetchOldEvents = () => axios.get(`${url}/old`);
export const participateEvent = (id, participant) => axios.post(`${url}/${id}/eventsignup`, participant);
export const subscribe = (email) => axios.post(`${sub_url}/subscribe`, email);
export const confirmSignup = (id, token) => axios.post(`${validate_url}/signup/${id}`, {key: token});
export const confirmUnsub = (token, email) => axios.post(`${validate_url}/unsub/${token}`);