import React, { useState, useEffect } from 'react';
import { Grid, CircularProgress, Typography } from '@material-ui/core'
import { useSelector } from 'react-redux';

import Event from './Event/Event.js';
import useStyles from './styles.js';

import { useLocale } from '../../Localization'

const Events = ({ searchString, setCurrentEnter }) => {
    const [count, setCount] = useState(0);
    const classes = useStyles();
    const locale = useLocale();
    const events = useSelector((state) => state.events);

    useEffect(() => {
        if (searchString && searchString !== "") setCount(events.filter((e) => e.title.toLowerCase().includes(searchString.toLowerCase())).length);
    }, [searchString, events.length])

    return (
        !events.length ? (<CircularProgress style={{margin: "0 auto"}}/>) : (
            <div style={{width: '100%', display: 'grid'}}>
                {searchString ? (
                <Typography className={classes.smMargin}>
                    {locale.searchResultCount.replace("$result$", count).replace("$total$", events.length)}
                </Typography>) : null}
                <Grid className={classes.mainContainer} container alignItems="stretch" direction="column" spacing={3} width="auto">
                    {events.map((event) => (
                        <Event key={event._id} event={event} searchString={searchString} setCurrentEnter={setCurrentEnter} />
                    ))}
                </Grid>
            </div>
        )
    );
}

export default Events;