import {makeStyles} from '@material-ui/core/styles';

export default makeStyles((theme) => ({
    root: {
      backgroundColor: 'whitesmoke',
    },
    languages: {
      position: 'absolute',
      top: '0',
      right: '0',
      padding: '1rem 0.5rem 0 0'
    },
    login: {
      position: 'absolute',
      left: 0,
      padding: '0 1rem'
    },
    appBar: {
      //borderRadius: 15,
      //margin: '30px 0',
      display: 'flex',
      flexDirection: 'row',
      justifyContent: 'center',
      alignItems: 'center',
      paddingTop: '0.5rem',
    },
    textHolder: {
      borderRadius: 15,
      padding: '1rem 0 1rem 0',
      margin: '30px 0',
      display: 'flex',
      alignItems: 'center',
      backgroundColor: 'whitesmoke',
    },
    heading: {
      color: 'white',
      width: '100%',
      margin: '-1rem 0 0 0',
      padding: '0.5rem 1.5rem 0.4rem 1.5rem',
      textAlign: 'center',      
      borderRadius: '15px 15px 0 0'
    },
    mainText: {
      marginTop: '0.5rem',
      whiteSpace: 'pre-line',
      [theme.breakpoints.up('lg')]: {
        maxWidth: '60%'
      }

    },
    image: {
      marginLeft: '15px',
    },
    filters: {
      margin: '1rem 0 2rem ',
      padding: '0 3rem',
      alignItems: 'center',
      display: 'flex',
      flexDirection: 'row',
      width: '100%',
      justifyContent: 'space-between'
    },
    search: {
      position: 'relative',
      "& .MuiOutlinedInput-root": {
        "& fieldset": { 
          borderColor: "#000fff",
          width: '12rem',
          transition: 'width 300ms cubic-bezier(0.4, 0, 0.2, 1) 0ms',
        },
        "&.Mui-focused fieldset": {
            width: '25rem',
        }}
    },
    supportWrapper: {
      position: 'fixed',
      left: '2%',
      bottom: '0',
    },
    supportButton: {
      marginBottom: '0.5rem'
    },
    supportPaper: {
      maxWidth: '50%',
      padding: '1rem 1rem'
    },
    [theme.breakpoints.down('sm')]: {
      mainContainer: {
        flexDirection: "column-reverse"
      }
    },
    loadOldButton: {
      display: 'flex',
      justifyContent: 'center',
      marginBottom: '3rem'
    },
    donateBox: {
      position: 'absolute',
      top: '10%',
      marginLeft: '2px',
      width: '200px',
      textAlign: 'center',
      [theme.breakpoints.down('md')]: {
        display: 'none'
      },
    },
    donateShowButton: {
      width: '100%',
      marginBottom: '1rem'
    }
      
}));