import { makeStyles } from '@material-ui/core/styles';

export default makeStyles((theme) => ({
  media: {
    paddingTop: '50%',
    position: 'relative',
    backgroundColor: 'rgba(0, 0, 0, 0.5)',
    backgroundBlendMode: 'darken',
    [theme.breakpoints.up('md')]: {
        width: '30%',
        paddingTop: 0
    }
  },
  border: {
      border: 'solid',
  },
  card: {
      display: 'flex',
      borderRadius: '15px',
      height: 'auto',
      position: 'relative',
      flexDirection: 'column',
      [theme.breakpoints.up('md')]: {
          flexDirection: 'row'
      }
  },
  content: {
    display: 'flex',
    flexDirection: 'column',
    width: '100%',
    justifyContent: 'space-between',
  },
  overlay: {
    position: 'absolute',
    top: '20px',
    left: '20px',
    color: 'white',
  },
  overlay2: {
    position: 'absolute',
    top: '20px',
    right: '20px',
    color: 'white',
  },
  overlay3: {
    position: 'absolute',
    bottom: '20px',
    left: '20px',
    color: 'white',
  },
  grid: {
    display: 'flex',
  },
  details: {
    display: 'flex',
    justifyContent: 'space-between',
    margin: '20px',
    marginBottom: '5px'
  },
  title: {
    padding: '0 16px',
  },
  cardActions: {
    padding: '0 16px 8px 16px',
    display: 'flex',
    justifyContent: 'space-between',
  },
  bodyContent: {
    whiteSpace: 'pre-line'
  }
}));