import React, { useState } from 'react';
import { Card, CardActions, CardContent, CardMedia, Button, Typography, Box, Grid} from '@material-ui/core';
import MoreHorizIcon from '@material-ui/icons/MoreHoriz';
import * as dayjs from 'dayjs';
import fromNow from 'dayjs/plugin/relativeTime'

import placeholder from '../../../images/placeholder.png';

import useStyles from './styles.js';
dayjs.extend(fromNow);

const OldEvent = ({ event }) => {
    const classes = useStyles();
    const [visible, setVisible] = useState(true);

    return ( !visible ? (null) : (
        <Grid item style={{width: '100%'}}>
        <Card className={classes.card}>
            <CardMedia className={classes.media} image={event.image ? event.image : placeholder} title={event.title} />
            <Box className={classes.content}>
                <div className={classes.overlay}>
                    <Typography variant="h6">{event.creator}</Typography>
                    <Typography variant="body2">{dayjs(event.createdDate).fromNow()}</Typography>
                </div>
                {event.lastModified ? <div className={classes.overlay3}>
                    <Typography variant="body2">Modified: {dayjs(event.lastModified).format('LLL')}</Typography>
                </div> : null}
                <div className={classes.overlay2}>
                    <Button style={{color: 'white'}} size="small" disabled >
                        <MoreHorizIcon fontSize="default" color="primary"/>
                    </Button>
                </div>
                <div className={classes.details}>
                    <Typography variant="body2" color="textSecondary">{event.tags.map((tag) => `#${tag} `)}</Typography>
                </div>
                <Typography className={classes.title} variant="h4" gutterBottom>{event.title}</Typography>
                <Typography className={classes.title} variant="h6" gutterBottom>When: [{dayjs(event.beginDate).format('LLL')}]</Typography>
                <Typography className={classes.title} variant="h6" gutterBottom>Where: {event.location}</Typography>
                <CardContent>
                    <Typography className={classes.bodyContent} variant="h6" color="textSecondary" component="p">{event.message}</Typography>
                </CardContent>
                <CardActions className={classes.cardActions}>
                    <Typography>
                        Room for {event.maxPartCount - event.partCount} people
                    </Typography>
                    <Typography>
                        Pricing: {event.price ? event.price : 0} €
                    </Typography>
                </CardActions>
            </Box>            
        </Card>
        </Grid>
    ));
}

export default OldEvent;