import React, {useState} from 'react';
import { Link } from 'react-router-dom'
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import { Checkbox, CircularProgress, FormControlLabel, Tooltip, Typography } from '@material-ui/core';

import { useDispatch } from 'react-redux';
import { subscribe } from '../../actions/events';
import { CheckCircle, Error, HelpOutline } from '@material-ui/icons';

const Success = () => {
    return (
        <DialogContent style={{width: '20rem', height: '10rem', display: 'flex', justifyContent: 'center', flexDirection: 'column'}}>
            <CheckCircle color="action" fontSize="large" style={{alignSelf: 'center', marginBottom: '1rem'}}/>
            <Typography style={{alignSelf: 'center'}}>Success, You have now subscribed to Vilkkoni's newsletter</Typography>
        </DialogContent>
    )
}

const Failure = () => {
    return (
        <DialogContent style={{width: '20rem', height: '10rem', display: 'flex', justifyContent: 'center', flexDirection: 'column'}}>
            <Error color="error" fontSize="large" style={{alignSelf: 'center', marginBottom: '1rem'}}/>
            <Typography style={{alignSelf: 'center'}}>An error has occured somewhere, please try again later</Typography>
        </DialogContent>
    )
}

const Subscribe = ({open, handleClose}) => {
    const [email, setEmail] = useState("");
    const [valid, setValid] = useState(true);
    const [visibleSub, setVisibleSub] = useState(false);
    const [loading, setLoading] = useState(false);
    const [success, setSuccess] = useState(false);
    const [sent, setSent] = useState(false);
    const dispatch = useDispatch();

    const handleInternalClose = () => {
        setEmail("");
        handleClose();
    }

    const handleInternalSub = () => {
        const re = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
        if (re.test(String(email).toLowerCase())) {
            setValid(true);
            dispatch(subscribe(email, visibleSub, subscribeCallback));
            setLoading(true);
            setSent(true);
        } else {
            setValid(false)
        }
    }

    const subscribeCallback = (res) => {
        setLoading(false);
        if (res === 200) setSuccess(true);
        setTimeout(() => {CloseDelayed()}, 1500);
    }

    const CloseDelayed = () => {
        setEmail("");
        handleClose();
        setSent(false);
        setSuccess(false);
    }

    return (
        <div>
            <Dialog open={open} aria-labelledby="form-dialog-title">
                {loading ? (
                    <DialogContent style={{width: '20rem', height: '10rem', display: 'flex', justifyContent: 'center',}}>
                        <CircularProgress style={{alignSelf: 'center'}} />
                    </DialogContent>
                ) : (
                !sent ? (
                <div>
                    <DialogTitle id="form-dialog-title">Subscribe</DialogTitle>
                    <DialogContent>
                    <DialogContentText>
                        To subscribe to this website, please enter your email address here. We will send You updates
                        on new events organized by Vilkkoni. You may unsubscribe at any time <Link to="/unsubscribe">here</Link> or by contacting support.
                    </DialogContentText>
                    <TextField
                        autoFocus
                        margin="dense"
                        id="email"
                        label="Email Address"
                        type="email"
                        fullWidth
                        value={email}
                        onChange={(e) => setEmail(e.target.value)}
                        error={!valid}
                        helperText={ valid ? null : "Invalid email" }
                    />
                    <div style={{display: 'flex'}}>
                        <FormControlLabel control={
                            <Checkbox checked={visibleSub} onClick={() => setVisibleSub(!visibleSub)}/>
                            }
                            label="Make my email address visible to Vilkkoni"
                        />
                        <Tooltip arrow title={<div style={{fontSize: '0.8rem'}}>When checked, Vilkkoni can see your email address. This does not affect your subscription</div>} placement="top" size="medium">
                            <HelpOutline style={{alignSelf: 'center'}} size="small" color="disabled" />
                        </Tooltip>
                    </div>
                    </DialogContent>
                    <DialogActions>
                    <Button onClick={handleInternalClose} color="primary">
                        Cancel
                    </Button>
                    <Button onClick={() => handleInternalSub()} color="primary">
                        Subscribe
                    </Button>
                    </DialogActions>
                </div>
                ) : (
                    success ? (<Success />) : (<Failure />)
                ))}
            </Dialog>
        </div>
    );
}

export default Subscribe;