import React, { useState, useEffect } from 'react';
import { Card, CardActions, CardContent, CardMedia, Button, Typography, Box, Grid} from '@material-ui/core';
import EventAvailable from '@material-ui/icons/EventAvailable';
import MoreHorizIcon from '@material-ui/icons/MoreHoriz';
import * as dayjs from 'dayjs';
import fromNow from 'dayjs/plugin/relativeTime'

import placeholder from '../../../images/placeholder.png';

import useStyles from './styles.js';
import { useLocale } from '../../../Localization';
dayjs.extend(fromNow);

const Event = ({ event, searchString, setCurrentEnter }) => {
    const classes = useStyles();
    const locale = useLocale('events');
    const [visible, setVisible] = useState(true);
    const [closed, setClosed] = useState(dayjs(event.beginDate).isBefore(dayjs().add(3, 'h')) || event.maxPartCount <= event.partCount);

    useEffect(() => {
        if (searchString !== null) setVisible(event.title.toLowerCase().includes(searchString.toLowerCase()));
    }, [searchString, event.title])

    return ( !visible ? (null) : (
        <Grid item style={{width: '100%'}}>
        <Card className={classes.card}>
            <CardMedia className={classes.media} image={event.image ? event.image : placeholder} title={event.title} />
            <Box className={classes.content}>
                <div className={classes.overlay}>
                    <Typography variant="h6">{event.creator}</Typography>
                    <Typography variant="body2">{dayjs(event.createdDate).fromNow()}</Typography>
                </div>
                {event.lastModified ? <div className={classes.overlay3}>
                    <Typography variant="body2">{locale.lastModified}{dayjs(event.lastModified).format('LLL')}</Typography>
                </div> : null}
                <div className={classes.overlay2}>
                    <Button style={{color: 'white'}} size="small" disabled >
                        <MoreHorizIcon fontSize="default" color="primary"/>
                    </Button>
                </div>
                <div className={classes.details}>
                    <Typography variant="body2" color="textSecondary">{event.tags.map((tag) => `#${tag} `)}</Typography>
                </div>
                <Typography className={classes.title} variant="h4" gutterBottom>{event.title}</Typography>
                <Typography className={classes.title} variant="h6" gutterBottom>{locale.time}{dayjs(event.beginDate).format('LLL')}</Typography>
                <Typography className={classes.title} variant="h6" gutterBottom>{locale.place}{event.location}</Typography>
                <CardContent>
                    <Typography className={classes.bodyContent} variant="h6" color="textSecondary" component="p">{event.message}</Typography>
                </CardContent>
                <CardActions className={classes.cardActions}>
                    <Typography>
                        {locale.room.replace("$number$", event.maxPartCount - event.partCount >= 0 ? event.maxPartCount - event.partCount : 0 )}
                    </Typography>
                    <Typography>
                        {locale.price.replace("$number$", event.price ? event.price : 0 )}
                    </Typography>
                    <Button size="medium" variant="contained" color="primary" align="right" disabled={closed} onClick={closed ? null : () => setCurrentEnter(event._id)}>
                        <EventAvailable fontSize="small"/>
                        &nbsp; {closed ? locale.enrollClosed : locale.enrollButton } &nbsp;
                    </Button>
                </CardActions>
            </Box>            
        </Card>
        </Grid>
    ));
}

export default Event;